<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">DatePicker</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text"></div>
            </li>
            <li>
                <h3>Demo</h3>
                <p class="btn" @click.stop="onClickDate()">demo</p>
                <div class="text">
                    <input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Usage</h3>

<pre class="JS">
DatePicker.Init({
    //0, 1, 2, 3
    "type": "3",

    //()
    "title": '',

    //
    "maxYear": "2100",

    //
    "minYear": "1900",

    //()
    "separator": "-",

    //
    "defaultValue": '',

    "callBack": function (val) {
        //val
        self.nowVal = val;
    }
});
</pre>
            </li>
            <li>
                <h3>Path</h3>
                <div class="text">
                    </br>
                    'src/lib/datePicker/picker.css'
                    'src/lib/datePicker/datePicker.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import DatePicker from '../../lib/datePicker/datePicker.js'

	export default {
		name: 'datePicker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 
            onClickDate(){
                var self = this;
                DatePicker.Init({
                    "type": "3",//0, 1, 2, 3
                    "title": '',//()
                    "maxYear": "2100",//
                    "minYear": "1900",//
                    "separator": "-",//()
                    "defaultValue": self.nowVal,//
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    @import "../../lib/datePicker/picker.css";
</style>


