<template>
<div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">ViewImage</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text"></div>
            </li>
            <li>
                <h3>Demo</h3>
                <div class="view-image">
                    <div v-for="item in imageList" @click.stop="onClickViewImage(item)"><img :src="item.smallImage"></div>
                </div>
            </li>
            <li>
                <h3>Usage</h3>
<pre class="JS">
//bigImageSrc 
mobileModal.viewImage(bigImageSrc);

</pre>
            </li>
            <li>
                <h3>Path</h3>
                <div class="text">
                    
                    'src/lib/mobileModal/mobileModal.css'
                    'src/lib/mobileModal/mobileModal.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import mobileModal from '../../lib/mobileModal/mobileModal.js'
    export default {
		name: 'viewImage',
        data(){
            return {
                imageList: [
                    {
                        'smallImage': require('../../assets/images/m1.jpg'),
                        'bigImage': require('../../assets/images/5.jpg')
                    }, {
                        'smallImage': require('../../assets/images/m2.jpg'),
                        'bigImage': require('../../assets/images/2.jpg')
                    },{
                        'smallImage': require('../../assets/images/m3.jpg'),
                        'bigImage': require('../../assets/images/3.jpg')
                    },{
                        'smallImage': require('../../assets/images/m4.jpg'),
                        'bigImage': require('../../assets/images/4.jpg')
                    }
                ],
                nowVal: ''
            }
        },
        methods: {
            onClickViewImage(item){
                var self = this;
                mobileModal.viewImage(item.bigImage);
            }
        }
	}
</script>

<style scoped>
    @import "../../lib/mobileModal/mobileModal.css";
</style>


