<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">Picker</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text"></div>
            </li>
            <li>
                <h3>Demo</h3>
                <p class="btn" @click.stop="picker1()">demoData1</p>
                <p class="btn" @click.stop="picker2()">demoData2</p>
                <p class="btn" @click.stop="picker3()">demoData3</p>
                <div class="text">
                    <input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Usage</h3>
<pre class="JS">
// data
let demoData1 = [{
    "id": "1",
    "text": "",
    "data": ""
},{
    "id": "2",
    "text": "",
    "data": ""
}];
</pre>

<pre class="JS">
// data
let demoData2 = [{
    "id": "1",
    "text": "",
    "data": [{
        "id": "11",
        "text": "",
        "data": ""
    }]
},{
    "id": "2",
    "text": "",
    "data": [{
        "id": "21",
        "text": "",
        "data": ""
    }]
}];
</pre>

<pre class="JS">
// data
let demoData3 = [{
    "id": "1",
    "text": "",
    "data": [{
        "id": "11",
        "text": "",
        "data": [{
            "id": "111",
            "text": "",
            "data": ""
        }]
    }]
},{
    "id": "2",
    "text": "",
    "data": [{
        "id": "21",
        "text": "",
        "data": [{
            "id": "211",
            "text": "",
            "data": ""
        }]
    }]
}];
</pre>

<pre class="JS">
// 
Picker.Init({
    //()
    "data": demoData1,

    //()
    "title": '',

    //
    "defaultValue": self.nowVal,

    //(idtextdata)
    "keys": {
        "id": "id",
        "value": "text",
        "childData": "data"//3
    },

    "callBack": function (val) {
        // val
        self.nowVal = val;
    }
});
</pre>

            </li>
            <li>
                <h3>Path</h3>
                <div class="text">
                    </br>
                    'src/lib/picker/picker.css'
                    'src/lib/picker/picker.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import Picker from '../../lib/picker/picker.js'

	export default {
		name: 'picker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 
            picker1: function () {
                var self = this;

                // data
                let demoData1 = [{
                    "id": "1",
                    "text": "",
                    "data": ""
                },{
                    "id": "2",
                    "text": "",
                    "data": ""
                }];

                Picker.Init({
                    "data": demoData1,//()
                    "title": '',//()
                    "defaultValue": self.nowVal,//
                    "keys": {
                        "id": "id",
                        "value": "text",
                        "childData": "data"//3
                    },//(idtextdata)
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            },

            picker2: function () {
                var self = this;

                // data
                let demoData2 = [{
                    "id": "1",
                    "text": "",
                    "data": [{
                        "id": "11",
                        "text": "",
                        "data": ""
                    }]
                },{
                    "id": "2",
                    "text": "",
                    "data": [{
                        "id": "21",
                        "text": "",
                        "data": ""
                    }]
                }];

                Picker.Init({
                    "data": demoData2,//()
                    "title": '',//()
                    "defaultValue": self.nowVal,//
                    "keys": {
                        "id": "id",
                        "value": "text",
                        "childData": "data"//3
                    },//(idtextdata)
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            },

            picker3: function () {
                var self = this;

                // data
                let demoData3 = [{
                    "id": "1",
                    "text": "",
                    "data": [{
                        "id": "11",
                        "text": "",
                        "data": [{
                            "id": "111",
                            "text": "",
                            "data": ""
                        }]
                    }]
                },{
                    "id": "2",
                    "text": "",
                    "data": [{
                        "id": "21",
                        "text": "",
                        "data": [{
                            "id": "211",
                            "text": "",
                            "data": ""
                        }]
                    }]
                }];

                Picker.Init({
                    "data": demoData3,//()
                    "title": '',//()
                    "defaultValue": self.nowVal,//
                    "keys": {
                        "id": "id",
                        "value": "text",
                        "childData": "data"//3
                    },//(idtextdata)
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    @import "../../lib/picker/picker.css";
</style>


