<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">Confirm</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text">
                    
                </div>
            </li>
            <li>
                <h3>Demo</h3>
                <p class="btn" @click.stop="onClickDemo()">demo</p>
                <div class="text">
                    <input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Usage</h3>
<pre class="JS">
mobileModal.confirm(
    '',//

    '',//

    //
    function () {
        self.nowVal = '';
    },

    //
    function () {
        self.nowVal = '';
    }
);
</pre>
            </li>
            <li>
                <h3>Path</h3>
                <div class="text">
                    
                    'src/lib/mobileModal/mobileModal.css'
                    'src/lib/mobileModal/mobileModal.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import mobileModal from '../../lib/mobileModal/mobileModal.js'
    export default {
        name: 'confirm',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            onClickDemo(){
                var self = this;
                mobileModal.confirm('', '',
                    function () {
                        // ()
                        self.nowVal = '';
                    }, function () {
                        // ()
                        self.nowVal = '';
                    }
                );
            }
        }
    }
</script>

<style scoped>
    @import "../../lib/mobileModal/mobileModal.css";
</style>


