//
import $ from 'n-zepto'
import IScroll from 'iscroll/build/iscroll-probe'


export default (function(){
    var pickerController = {
        options: null,//
        scrollArray: [],//iscroll
        textArray: [],//
        isScrollTo: false,//scrollTo 
        monthLen: 30,// 

        //
        touchDefault: function (e) {
            e.preventDefault();
        },

        //&
        touchStop : function (e) {
            e.stopPropagation();
            e.preventDefault();
        },

        //
        add_EventListen: function () {
            var self = this;

            // document.addEventListener('touchend', self.touchDefault, false);
            // document.addEventListener('touchstart', self.touchDefault, false);
            document.addEventListener('touchmove', self.touchDefault, false);
        },

        //
        remove_EventListen: function () {
            var self = this;

            // document.removeEventListener('touchend', self.touchDefault, false);
            // document.removeEventListener('touchstart', self.touchDefault, false);
            document.removeEventListener('touchmove', self.touchDefault, false);
        },

        //iscroll
        scrollInit: function (index, num) {
            var self = this;

            var id = '#wrapper' + index;

            //iscroll 
            self.scrollArray[index] = new IScroll(id, {
                probeType: 3,
                scrollX: false,
                scrollY: true,
                useTransition: true,
                HWCompositing: true,
                disableMouse: true,
                disablePointer: true,
            });

            //move
            self.add_EventListen();

            //
            var itemHeight = $(".zx_select").find("li").eq(1).height();

            if (num) {
                self.scrollArray[index].scrollTo(0, -itemHeight * num, 0);
            }

            self.scrollSite(index, itemHeight);
        },

        //
        scrollSite: function (type, itemHeight) {
            var self = this;

            var myScroll = self.scrollArray[type];

            //
            var nowEndY = 0;

            //
            myScroll.on('scrollStart', function () {
                $(".zx_select *").on("touchstart", self.touchStop);
            });

            //
            myScroll.on('scroll', function () {
                if (self.isScrollTo) {
                    return;
                }

                var moveingY = parseInt(this.y);

                var index = parseInt(Math.abs(moveingY) / itemHeight);//item index
                var h = Math.abs(moveingY % itemHeight);//
                var halfHeight = itemHeight / 2;//

                var wd_height = document.body.scrollHeight - 1;

                //
                if (h > halfHeight) {
                    index += 1;
                }

                if ( this.pointY < 1 ) {
                    var domItem = $(this.scroller).children("li:last-child");//
                    self.SetItemList(type, domItem);

                    self.isScrollTo = true;
                    this.scrollTo(0, this.maxScrollY, 0);
                } else if ( this.pointY > wd_height ) {
                    var domItem = $(this.scroller).children("li:first-child");//
                    self.SetItemList(type, domItem);

                    self.isScrollTo = true;
                    this.scrollTo(0, 0, 0);
                }
            });

            //
            myScroll.on('scrollEnd', function () {
                $(".zx_select *").off("touchstart", self.touchStop);

                if (self.isScrollTo) {
                    self.isScrollTo = false;
                    return;
                }

                nowEndY = this.y;

                var h = Math.abs(nowEndY % itemHeight);//
                var halfHeight = itemHeight / 2;//

                //
                if (h > halfHeight) {
                    var moveY = nowEndY - itemHeight + h;
                } else {
                    var moveY = nowEndY + h;
                }

                var index = parseInt(Math.abs(moveY) / itemHeight);//item index
                var domItem = $(this.scroller).children("li").eq(index);//
                self.SetItemList(type, domItem);

                // this.scrollTo(0, moveY, 0);
                this.scrollTo(0, moveY, 0);

                if( self.options.type == 3 ){
                    var nowPlace = self.textArray[2].value;
                    if( nowPlace > self.monthLen ){
                        var hiddenLen = nowPlace - self.monthLen;
                        var moveLen = self.scrollArray[2].y + hiddenLen * itemHeight;
                        self.textArray[2].value = self.monthLen;
                        self.scrollArray[2].scrollTo(0, moveLen, 0);
                    }
                }
            });
        },

        //:(1)4100(2)400
        isLeap : function (year) {
            if( (year % 4 == 0 && year % 100 != 0) || ( year % 400 == 0 ) ){
                return 1;
            }
            return 0;
        },

        //
        getMonthLength: function () {
            var self = this;
            if ( self.options.type == 3 ) {
                var nowYear = self.textArray[0].value;
                var nowMonth = self.textArray[1].value;
                var leap = self.isLeap(nowYear);

                if( nowMonth == "02" ){
                    self.monthLen = 28 + leap;
                }else if( nowMonth == "01" || nowMonth == "03" || nowMonth == "05" || nowMonth == "07" || nowMonth == "08" || nowMonth == "10" || nowMonth == "12" ){
                    self.monthLen = 31;
                }else {
                    self.monthLen = 30;
                }
            }
        },

        //
        SetItemList: function (type, domItem) {
            var self = this;

            if (domItem) {
                var itemObject = {};
                itemObject.value = domItem.attr("data-value");
            } else {
                var itemObject = "";
            }

            self.textArray[type] = itemObject;
            self.getMonthLength();
        },

        //data
        FillData: function () {
            var self = this;

            //bodypicker
            if( $(".zx_select").length > 0 ){
                $(".zx_mask").remove();
            }

            var title = self.options.title;
            var picker_list = '<div class="zx_mask"><div class="zx_select showPicker">' +
                '<header><button class="nav_left"></button>' +
                '<h1>' + title + '</h1><button class="nav_right"></button></header>' +
                '<div class="ub"><div class="ub-f1" id="wrapper0"><ul></ul></div>';

            //2
            if( self.options.type > 0 ) {
                picker_list += '<div class="ub-f1" id="wrapper1"><ul></ul></div>';
            }

            //3
            if( self.options.type > 2 ){
                picker_list += '<div class="ub-f1" id="wrapper2"><ul></ul></div>';
            }

            picker_list += '<div class="sel_top"></div><div class="sel_bottom"></div>';
            picker_list += '<div class="sel_middle"></div></div></div></div>';
            $("body").append(picker_list);

            //
            var listWidth = parseFloat(100 / self.options.num).toFixed(3) + "%";
            if( self.options.type == 1 || self.options.type == 2 ){
                $(".zx_select").children("div").css({
                    "padding": "0 15%"
                });
            }

            $(".zx_select").children("div").children("div.ub-f1").css({
                "font-size": "0.38rem",
                "max-width": listWidth,
                "min-width": listWidth
            });

            //
            var df_0, df_1, df_2;
            if (self.options.defaultValue && self.options.defaultValue != "") {
                var defaultValue = self.options.defaultValue;
                var separator = self.options.separator;
                defaultValue += "";
                df_0 = defaultValue.split(separator)[0];
                df_1 = defaultValue.split(separator)[1];
                df_2 = defaultValue.split(separator)[2];
            }

            switch(self.options.type){
                case "1":
                    var num = self.getYearList(0, df_0);
                    var num1 = self.getMonthList(1, df_1);
                    self.scrollInit(0, num);
                    self.scrollInit(1, num1);
                    break;
                case "2":
                    var num = self.getMonthList(0, df_0);
                    var num1= self.getDayList(1, df_1);
                    self.scrollInit(0, num);
                    self.scrollInit(1, num1);
                    break;
                case "3":
                    var num = self.getYearList(0, df_0);
                    var num1 = self.getMonthList(1, df_1);
                    var num2 = self.getDayList(2, df_2);
                    self.scrollInit(0, num);
                    self.scrollInit(1, num1);
                    self.scrollInit(2, num2);
                    break;
                case "0":
                default:
                    var num = self.getYearList(0, df_0);
                    self.scrollInit(0, num);
                    break;
            }

            $(".zx_select").css("height", "4.9rem");
        },

        //
        setDefaultItem: function (index, dateValue) {
            var self = this;
            var itemObject = {};
            itemObject.value = dateValue;
            self.textArray[index] = itemObject;
        },

        //
        getYearList: function (index, defaultValue) {
            var self = this;
            var list = "";
            var defaultNum;//

            var maxYear = self.options.maxYear;
            var minYear = self.options.minYear;
            if( defaultValue ){
                var num = 0, isMatch = false;
                for (var i = maxYear; i > minYear; i--) {
                    if ( defaultValue == i ) {
                        isMatch = true;
                        defaultNum = num;//
                        self.setDefaultItem(index, i);
                    }

                    list += '<li data-value="'+ i +'">' + i + '</li>';
                    num++;
                }

                if( !isMatch ){
                    self.setDefaultItem(index, maxYear);
                }
            }else {
                for (var i = maxYear; i > minYear; i--) {
                    list += '<li data-value="'+ i +'">' + i + '</li>';
                }
                self.setDefaultItem(index, maxYear);
            }

            $("#wrapper" + index).children("ul").html(list);
            return defaultNum;
        },

        //
        getMonthList: function (index, defaultValue) {
            var self = this;
            var list = "";
            var defaultNum;//

            var unit = "", prefix = "0";
            if( self.options.type == 2 ){
                unit = "";
                prefix = "";
            }

            if( defaultValue ){
                var num = 0, isMatch = false;
                for (var i = 1; i <= 12; i++) {
                    var count = i < 10 ? prefix + i : i;
                    if ( defaultValue == i ) {
                        isMatch = true;
                        defaultNum = num;//
                        self.setDefaultItem(index, count);
                    }

                    list += '<li data-value="'+ count +'">' + count + unit +'</li>';
                    num++;
                }

                if( !isMatch ){
                    self.setDefaultItem(index, '01');
                }
            }else {
                for (var i = 1; i <= 12; i++) {
                    var count = i < 10 ? prefix + i : i;
                    list += '<li data-value="'+ count +'">' + count + unit +'</li>';
                }
                self.setDefaultItem(index, prefix + 1);
            }

            $("#wrapper" + index).children("ul").html(list);
            return defaultNum;
        },


        //
        getDayList: function (index, defaultValue) {
            var self = this;
            var list = "";
            var defaultNum;//

            var unit = "", prefix = "0";
            if( self.options.type == 2 ){
                unit = "";
                prefix = "";
            }

            if( defaultValue ){
                var num = 0, isMatch = false;
                for (var i = 1; i <= 31; i++) {
                    var count = i < 10 ? prefix + i : i;
                    if ( defaultValue == count ) {
                        isMatch = true;
                        defaultNum = num;//
                        self.setDefaultItem(index, count);
                    }

                    list += '<li data-value="'+ count +'">' + count + unit +'</li>';
                    num++;
                }

                if( !isMatch ){
                    self.setDefaultItem(index, '01');
                }
            }else {
                for (var i = 1; i <= 31; i++) {
                    var count = i < 10 ? prefix + i : i;
                    list += '<li data-value="'+ count +'">' + count + unit +'</li>';
                }
                self.setDefaultItem(index, prefix + 1);
            }

            $("#wrapper" + index).children("ul").html(list);
            return defaultNum;
        },

        //picker
        HidePicker : function(){
            var self = this;

            $(".zx_select").css("height", "0");
            self.remove_EventListen();

            setTimeout(function () {
                $(".zx_mask").remove();
                self.destroy();//
            }, 200);
        },

        //
        eventClick: function () {
            var self = this;

            var headerButton = $(".zx_select").children("header");

            //
            headerButton.children("button.nav_left").on("touchstart", function (e) {
                e.stopPropagation();
                e.preventDefault();
                //picker
                self.HidePicker();
            });

            //
            headerButton.children("button.nav_right").on("touchstart", function (e) {
                e.stopPropagation();
                e.preventDefault();

                var inputValue = '';
                for (var i = 0; i < self.textArray.length; i++) {
                    if (i == 0) {
                        inputValue += self.textArray[i].value;
                    } else {
                        inputValue += self.options.separator + self.textArray[i].value;
                    }
                }

                // & code
                // self.options.picker.val(inputValue);
                // self.options.picker.value = inputValue;
                self.options.callBack(inputValue);

                //picker
                self.HidePicker();
            });

            //mask
            // $(".zx_mask").on("click", function (e) {
            //     e.stopPropagation();
            //     e.preventDefault();
            //     //picker
            //     self.HidePicker();
            // });

            // $(".zx_mask *").on("click", function (e) {
            //     e.stopPropagation();
            // });
        },

        //
        setDefaultOptions: function (params) {
            var self = this;

            //
            var date = new Date();
            var nowYear = date.getFullYear();
            var nowMonth = date.getMonth() + 1;
            var nowDay = date.getDate();

            //
            params.separator = params.separator ? params.separator : "/";

            nowMonth = nowMonth < 10 ? "0" + nowMonth : nowMonth;
            nowDay = nowDay < 10 ? "0" + nowDay : nowDay;
            if( !params.defaultValue || params.defaultValue == "" ){
                if( params.type == 0 ){
                    params.defaultValue = nowYear;
                }else if( params.type == 1 ){
                    params.defaultValue = nowYear + params.separator + nowMonth;
                }else if( params.type == 2 ){
                    params.defaultValue = 7 + params.separator + 28;
                }else if( params.type == 3 ){
                    params.defaultValue = nowYear + params.separator + nowMonth + params.separator + nowDay;
                }
            }

            //
            params.maxYear = params.maxYear ? params.maxYear : nowYear;
            params.minYear = params.minYear ? params.minYear : 1900;

            //
            self.options = params;
        },

        //picker
        destroy: function () {
            var self = this;
            self.options = null;//
            self.scrollArray = [];//iscroll
            self.textArray = [];//
            self.isScrollTo = false;//scrollTo 
            self.monthLen = 30;
        }
    };

    return {
        Init : function(params){
            // 
            $('input').blur();
            $('textarea').blur();

            //set
            pickerController.setDefaultOptions(params);

            //picker
            pickerController.FillData();

            //click
            pickerController.eventClick();
        }
    }
}());



