import $ from 'n-zepto'
import IScroll from 'iscroll/build/iscroll-probe'

//
export default (function(){
    var pickerController = {
        dataListTwo: null,//
        dataListThree: null,//

        options: null,//
        arrayDepth : 1,//

        scrollArray: [],//iscroll
        textArray: [],//
        isScrollTo: false,//scrollTo 

        //
        touchDefault: function (e) {
            e.preventDefault();
        },

        //&
        touchStop : function (e) {
            e.stopPropagation();
            e.preventDefault();
        },

        //
        add_EventListen: function () {
            var self = this;

            // document.addEventListener('touchend', self.touchDefault, false);
            // document.addEventListener('touchstart', self.touchDefault, false);
            document.addEventListener('touchmove', self.touchDefault, false);
        },

        //
        remove_EventListen: function () {
            var self = this;

            // document.removeEventListener('touchend', self.touchDefault, false);
            // document.removeEventListener('touchstart', self.touchDefault, false);
            document.removeEventListener('touchmove', self.touchDefault, false);
        },

        //iscroll
        scrollInit: function (index, num) {
            var self = this;

            var id = '#wrapper' + index;

            //iscroll 
            self.scrollArray[index] = new IScroll(id, {
                probeType: 3,
                scrollX: false,
                scrollY: true,
                useTransition: true,
                HWCompositing: true,
                disableMouse: true,
                disablePointer: true,
            });

            //move
            self.add_EventListen();

            //
            var itemHeight = $(".zx_select").find("li").eq(0).height() / 3;

            if (num) {
                self.scrollArray[index].scrollTo(0, -itemHeight * num, 0);
            }

            self.scrollSite(index, itemHeight);
        },

        //
        scrollSite: function (type, itemHeight) {
            var self = this;

            var myScroll = self.scrollArray[type];

            //
            var nowEndY = 0;

            //
            myScroll.on('scrollStart', function () {
                $(".zx_select *").on("touchstart", self.touchStop);
            });

            //
            myScroll.on('scroll', function () {
                if (self.isScrollTo) {
                    return;
                }

                // var moveingY = parseInt(this.y);
                // var index = parseInt(Math.abs(moveingY) / itemHeight);//item index
                // var h = Math.abs(moveingY % itemHeight);//
                // var halfHeight = itemHeight / 2;//
                var wd_height = document.body.scrollHeight - 1;

                //
                // if (h > halfHeight) {
                //     index += 1;
                // }

                if ( this.pointY < 1 ) {
                    var domItem = $(this.scroller).children("li:last-child");//
                    self.SetItemList(domItem, type);

                    self.isScrollTo = true;
                    this.scrollTo(0, this.maxScrollY, 0);
                } else if ( this.pointY > wd_height ) {
                    var domItem = $(this.scroller).children("li:first-child");//
                    self.SetItemList(domItem, type);

                    self.isScrollTo = true;
                    this.scrollTo(0, 0, 0);
                }
            });

            //
            myScroll.on('scrollEnd', function () {
                $(".zx_select *").off("touchstart", self.touchStop);

                if (self.isScrollTo) {
                    self.isScrollTo = false;
                    return;
                }

                nowEndY = this.y;
                var h = Math.abs(nowEndY % itemHeight);//
                var halfHeight = itemHeight / 2;//

                //
                if (h > halfHeight) {
                    var moveY = nowEndY - itemHeight + h;
                } else {
                    var moveY = nowEndY + h;
                }

                var index = parseInt(Math.abs(moveY) / itemHeight);//item index
                var domItem = $(this.scroller).children("li").eq(index);//
                self.SetItemList(domItem, type);

                this.scrollTo(0, moveY, 0);
                // this.scrollTo(0, moveY, 300, IScroll.utils.ease.quadratic);
            });
        },

        //
        SetItemList: function (domItem, type) {
            var self = this;

            if (domItem) {
                //
                var keys = self.options.keys;
                var id = keys.id;
                var value = keys.value;

                var itemObject = {};
                itemObject[value] = domItem.text();
                itemObject[id] = domItem.attr("data-id");

                if (type == 0) {
                    //1
                    self.getDataList(0, itemObject[value]);

                    //2
                    if( self.options.num >= 2 ){
                        self.getDataList(1);
                        self.scrollArray[1].refresh();
                        self.scrollArray[1].scrollTo(0, 0, 0);
                    }

                    //3
                    if( self.options.num >= 3 ){
                        self.getDataList(2);
                        self.scrollArray[2].refresh();
                        self.scrollArray[2].scrollTo(0, 0, 0);
                    }
                } else if (type == 1) {
                    self.getDataList(1, itemObject[value]);

                    if( self.options.num >= 3 ){
                        //3
                        self.getDataList(2);
                        self.scrollArray[2].refresh();
                        self.scrollArray[2].scrollTo(0, 0, 0);
                    }
                }
            } else {
                var itemObject = "";
            }

            self.textArray[type] = itemObject;
        },

        //data
        FillData: function () {
            var self = this;

            //bodypicker
            if( $(".zx_select").length > 0 ){
                $(".zx_mask").remove();
            }

            var title = self.options.title;
            var picker_list = '<div class="zx_mask"><div class="zx_select showPicker">' +
                '<header><button class="nav_left"></button>' +
                '<h1>' + title + '</h1><button class="nav_right"></button></header>' +
                '<div class="ub"><div class="ub-f1" id="wrapper0"><ul></ul></div>';

            //2
            if( self.options.num >= 2 ) {
                picker_list += '<div class="ub-f1" id="wrapper1"><ul></ul></div>';
            }

            //3
            if( self.options.num >= 3 ){
                picker_list += '<div class="ub-f1" id="wrapper2"><ul></ul></div>';
            }

            picker_list += '<div class="sel_top"></div><div class="sel_bottom"></div>';
            picker_list += '<div class="sel_middle"></div></div></div></div>';
            $("body").append(picker_list);

            //
            var listWidth = parseFloat(100 / self.options.num).toFixed(3) + "%";
            $(".zx_select").children("div").children("div.ub-f1").css({"max-width" : listWidth, "min-width" : listWidth});

            //
            var par_name0, par_name1, par_name2;
            if (self.options.defaultValue) {
                var defaultValue = self.options.defaultValue;
                par_name0 = defaultValue.split(" ")[0];
                par_name1 = defaultValue.split(" ")[1];
                par_name2 = defaultValue.split(" ")[2];
            }

            //1
            if( self.options.num >= 1 ) {
                var num0 = self.getDataList(0, par_name0);
                self.scrollInit(0, num0);
            }

            //2
            if( self.options.num >= 2 ) {
                var num1 = self.getDataList(1, par_name1);
                self.scrollInit(1, num1);
            }

            //3
            if( self.options.num >= 3 ){
                var num2 = self.getDataList(2, par_name2);
                self.scrollInit(2, num2);
            }

            $(".zx_select").css("height", "4.9rem");
        },

        //
        setDefaultItem: function (index, name, code) {
            var self = this;

            //
            var keys = self.options.keys;
            var id = keys.id;
            var value = keys.value;

            var itemObject = {};
            itemObject[value] = name;
            itemObject[id] = code;

            self.textArray[index] = itemObject;
        },

        //
        getDataList: function (index, par_name) {
            var self = this;

            //
            var keys = self.options.keys;
            var id = keys.id;
            var value = keys.value;
            var childData = keys.childData;

            var list = "";
            var defaultNum;//

            if(index == 0){
                var data = self.options.data;
            }else if(index == 1){
                var data = self.dataListTwo;
            }else if(index == 2){
                var data = self.dataListThree;
            }

            if (par_name) {
                //
                var isMatch = false;// 
                for (var i = 0; i < data.length; i++) {
                    if ( par_name == data[i][value] ) {
                        isMatch = true;
                        defaultNum = i;//
                        self.setDefaultItem(index, data[i][value], data[i][id]);

                        //
                        if(index == 0 && childData != ""){
                            self.dataListTwo = data[i][childData];
                        }else if(index == 1 && childData != ""){
                            self.dataListThree = data[i][childData];
                        }
                    }

                    list += '<li data-id="' + data[i][id] + '">' + data[i][value] + '</li>';
                }

                if( !isMatch ){
                    //
                    if(index == 0 && childData != ""){
                        self.dataListTwo = data[0][childData];
                    }else if(index == 1 && childData != ""){
                        self.dataListThree = data[0][childData];
                    }
                    self.setDefaultItem(index, data[0][value], data[0][id]);
                }
            } else {
                //
                for (var i = 0; i < data.length; i++) {
                    if (i == 0) {
                        self.setDefaultItem(index, data[i][value], data[i][id]);

                        //
                        if(index == 0 && childData != ""){
                            self.dataListTwo = data[i][childData];
                        }else if(index == 1 && childData != ""){
                            self.dataListThree = data[i][childData];
                        }
                    }

                    list += '<li data-id="' + data[i][id] + '">' + data[i][value] + '</li>';
                }
            }

            $("#wrapper" + index).children("ul").html(list);
            return defaultNum;
        },

        //picker
        HidePicker : function(){
            var self = this;

            $(".zx_select").css({"height":"0"});
            self.remove_EventListen();

            setTimeout(function () {
                $(".zx_mask").remove();
                self.destroy();//
            }, 200);
        },

        //
        eventClick: function () {
            var self = this;

            var headerButton = $(".zx_select").children("header");

            //
            headerButton.children("button.nav_left").on("touchstart", function (e) {
                e.stopPropagation();
                e.preventDefault();
                //picker
                self.HidePicker();
            });

            //
            headerButton.children("button.nav_right").on("touchstart", function (e) {
                e.stopPropagation();
                e.preventDefault();

                var inputValue = '';
                var dataCode = '';

                var keys = self.options.keys;
                for (var i = 0; i < self.textArray.length; i++) {
                    var id = keys.id;
                    var value = keys.value;

                    if (i == 0) {
                        inputValue += self.textArray[i][value];
                        dataCode += self.textArray[i][id];
                    } else {
                        inputValue += " " + self.textArray[i][value];
                        dataCode += "," + self.textArray[i][id];
                    }
                }

                // & code
                // self.options.picker.val(inputValue).attr("data-id", dataCode);
                // $(self.options.picker).val(inputValue).attr("data-id", dataCode);
                self.options.callBack(inputValue);

                //picker
                self.HidePicker();
            });

            //mask
            // $(".zx_mask").on("click", function (e) {
            //     e.stopPropagation();
            //     e.preventDefault();
            //     //picker
            //     self.HidePicker();
            // });

            // $(".zx_mask *").on("click", function (e) {
            //     e.stopPropagation();
            // });
        },

        //
        getArrayDepth : function (data){
            var self = this;
            var dataArray = data[0];
            var index = "";

            for(var i in dataArray){
                if( Array.isArray(dataArray[i]) && dataArray[i].length != 0 ){
                    index = i;
                    self.arrayDepth++;
                    break;
                }
            }

            if( index ){
                self.getArrayDepth(dataArray[index]);
            }
        },

        //picker
        destroy: function () {
            var self = this;
            self.dataListTwo = null;//
            self.dataListThree = null;//
            self.options = null;//
            self.arrayDepth = 1;//
            self.scrollArray = [];//iscroll
            self.textArray = [];//
            self.isScrollTo = false;//scrollTo 
        }
    };


    return {
        Init : function(params){
            // 
            $('input').blur();
            $('textarea').blur();

            //
            pickerController.arrayDepth = 1;
            pickerController.getArrayDepth(params.data);
            params.num = pickerController.arrayDepth;

            //
            pickerController.options = params;

            //picker
            pickerController.FillData();

            //click
            pickerController.eventClick();
        }
    }
}());



